

.score{
    margin-top: 1em;
    width: 100%;
    padding: 5px;
    display: block;
    margin-bottom: 2em;
    display: flex;
    justify-content: center;
}

.score div{
    background-color: rgb(233, 233, 135);
    width: 20%;
    border: 2px black solid;
    border-radius: 1em;
    padding: 0.6em;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-size: 20px;
}

.score div p{
    margin: 0;
}